import css from 'styled-jsx/css';


const Output = (props) => {


    return (
        <div>
            <div id='preview' dangerouslySetInnerHTML={props.setMarkdown}>
            </div>
                <style jsx>{`
                #preview{
                    width: 800px;
                    margin: 50px auto;
                    border: 5px solid black;
                }
                #preview {
                    border-collapse: collapse;
                }
                #preview,
                #preview {
                    border: 2px solid gray;
                    padding-left: 5px;
                    padding-right: 5px;
                }
                `}</style>
        </div>
    )
}

const {tableStyle, styles} = css.resolve`a { border: 2p solid black}`;

export default () =><div>
    <Output className={tableStyle} />
    {styles}
</div>;